<template>
    <div class="setting">
        <div class="header">
            <span @click="back" class="back">
                <img src="../assets/img/back.png" class="releaseIcon">
            </span>
            <span class="usersTitle">设置</span>
            <span class="back"></span>
        </div>
        <div class="security">
            <div class="securityTitle">安全设置</div>
            <div class="phoneNum">
                <span>手机号</span>
                <span class="userPhone">
                    <span>{{ phone }}</span>
                    <span class="next">
                        <img src="../assets/img/next.png">
                    </span>
                </span>
            </div>
            <div class="userPwd" @click="changePwd">
                <span>登录密码设置</span>
                <span class="next">
                    <img src="../assets/img/next.png">
                </span>
            </div>
        </div>
        <div class="accountSetting">
            <div class="accountTitle">账号绑定</div>
            <div class="bindTos">
                <div class="bindTo" v-for="(account, index) in accounts" :key="index">
                    <span>{{ account.type }}</span>
                    <span class="binding">
                        <span @click="toggleBind(index)" :class="account.bind === true ? 'bind' : 'notbind'">{{ account.bind === true ? '已绑定' : '未绑定' }}</span>
                        <span class="next">
                            <img src="../assets/img/next.png">
                        </span>
                    </span>
                </div>
            </div>
        </div>
        <div class="advice" @click="advice">
            <span>意见反馈</span>
            <span class="nextIcon">
                <img src="../assets/img/next.png">
            </span>
        </div>
        <div class="clearCache">
            <span>清除缓存</span>
            <span class="nextIcon">
                <img src="../assets/img/next.png">
            </span>
        </div>
        <div class="aboutUs" @click="about">
            <span>关于我们</span>
            <span class="nextIcon">
                <img src="../assets/img/next.png">
            </span>
        </div>
        <mt-button @click="logOut" class="logOut">退出登录</mt-button>
    </div>
</template>

<script>
import { logout, getUserInfo } from '../api/getdata'
import { MessageBox, Toast } from 'mint-ui'
// import { getCookie, setCookie } from '../../static/js/cookieUtil'
export default {
  name: 'setting',
  data () {
    return {
      userId: '',
      myInfo: {},
      phone: '',
      accounts: [{
        type: '微信账号', bind: true
      }, {
        type: '微博账号', bind: false
      }, {
        type: 'QQ账号', bind: false
      }]
    }
  },
  created () {
    this.userId = localStorage.getItem('userId')
    // console.log(this.userType)
    let data = getUserInfo({fUserId: this.userId})
    data.then(res => {
      // console.log(res.data)
      this.myInfo = res.data
      this.phone = this.myInfo.userMobile.replace(/(\d{3})\d{6}(\d{2})/, '$1******$2')
      // console.log(this.myInfo)
    })
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    logOut () {
      localStorage.removeItem('userState')
      logout().then(res => {
        // console.log(res)
        setTimeout(() => {
          this.$router.push('/home/login')
        }, 1000)
        if (res.status === '0') {
          Toast({
            message: '已退出登录',
            duration: 1500
          })
        }
        // console.log(getCookie('userState'))
      })
    },
    toggleBind (index) {
      if (this.accounts[index].bind === true) {
        MessageBox.confirm('', {
          message: '确定取消绑定？'
        }).then(action => {
          if (action === 'confirm') {
            this.accounts[index].bind = !this.accounts[index].bind
            Toast({
              message: '取消绑定成功',
              duration: 1000
            })
          }
        })
      } else {
        this.accounts[index].bind = !this.accounts[index].bind
        Toast({
          message: '绑定成功',
          duration: 1000
        })
      }
    },
    advice () {
      this.$router.push('/home/mine/advice')
    },
    about () {
      this.$router.push('/home/mine/about')
    },
    changePwd () {
      this.$router.push('/home/mine/changePwd')
    }
  }
}
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
    font-size: 0.426667rem;
  }

  .header {
    height: 1.5rem;
    display: flex;
    justify-content: space-between;
    position: relative;
    font-size: 0.48rem;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .back {
    height: 0.5rem;
    width: 0.266667rem;
  }

  .back img {
    width: 100%;
  }

  .logOut {
    margin-top: 0.5rem;
    width: calc(100% - 0.8rem);
  }

  .security,
  .accountSetting {
    margin-top: 0.373333rem;
    width: calc(100% - 0.8rem);
    padding: 0 0.4rem;
    border-bottom: 0.32rem solid #f5f5f5;
  }

  .securityTitle,
  .accountTitle {
    width: 100%;
    text-align: start;
  }

  .phoneNum,
  .userPwd,
  .bindTo {
    width: calc(100% - 0.4rem);
    height: 1.3333rem;
    padding-left: 0.4rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .binding {
    display: flex;
  }
  .next {
    width: 0.2333rem;
    margin-left: 0.373333rem;
  }

  .nextIcon {
    width: 0.2333rem;
    display: flex;
    align-items: center;
  }

  .userPhone,
  .bindState {
    display: flex;
    align-items: center;
  }

  .userPhone span,
  .bindState span {
    display: flex;
    align-items: center;
  }

  img {
    width: 100%;
  }

  .phoneNum {
    border-bottom: 1px solid #f5f5f5;
  }

  .bind {
    color: #333333;
    display: flex;
  }
  .notbind {
    display: flex;
    color: #ff8309;
  }

  .advice,
  .clearCache,
  .aboutUs {
    height: 1.3333rem;
    width: calc(100% - 0.8rem);
    padding: 0 0.4rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .bindTo:nth-child(2),
  .clearCache {
    border-top: 1px solid #f5f5f5;
    border-bottom: 1px solid #f5f5f5;
  }

  .logOut {
    margin-top: 1rem;
    color: #f5f5f5;
    background: linear-gradient(to right bottom, #ff8309, #ffd200);
    height: 1.3333rem;
  }
</style>



